.grid-experience .head-collapse {
  padding-top: 60px; }
  @media screen and (max-width: 768px) {
    .grid-experience .head-collapse svg {
      font-size: 10em; } }
  @media screen and (min-width: 768px) and (max-width: 992px) {
    .grid-experience .head-collapse svg {
      font-size: 6em; } }
  @media screen and (min-width: 992px) {
    .grid-experience .head-collapse svg {
      font-size: 8em; } }
  .grid-experience .head-collapse h5 {
    margin: 0;
    padding: 1em; }
  .grid-experience .head-collapse.has-body h5:after {
    content: "";
    font-family: "Font Awesome 5 Free";
    font-weight: 900;
    margin-left: 10px; }
  .grid-experience .head-collapse.has-body:not(.expanded):hover svg {
    -webkit-animation: grow-text 0.5s ease-in-out 0s alternate forwards;
