    animation: grow-text 0.5s ease-in-out 0s alternate forwards; }
  .grid-experience .head-collapse.has-body:not(.expanded):hover h5 {
    color: #17A2B8; }
  .grid-experience .head-collapse.expanded {
    -webkit-animation: transition-expand-light 0.5s ease-in-out 0s alternate forwards;
    -moz-animation: transition-expand-light 0.5s ease-in-out 0s alternate forwards;
    animation: transition-expand-light 0.5s ease-in-out 0s alternate forwards; }
    .grid-experience .head-collapse.expanded svg {
