      -ms-transform: scale(1.1);
      transform: scale(1.1); }
    .grid-experience .head-collapse.expanded h5:after {
      -webkit-animation: animate-toggle 0.2s linear 0s alternate forwards;
      -moz-animation: animate-toggle 0.2s linear 0s alternate forwards;
      animation: animate-toggle 0.2s linear 0s alternate forwards; }

.grid-experience .body-collapse {
